<template>
  <el-row class="demo-autocomplete">
  <el-col :span="24" >
    <el-autocomplete
      class="inline-input"
      v-model="state1"
      :fetch-suggestions="querySearch"
      placeholder=""
      @select="handleSelect"
    ></el-autocomplete>
    <el-button style="margin-left:20px"></el-button>
    <el-button></el-button>
  </el-col>
  </el-row>

  
</template>
<script>
export default {
    data() {
      return {
        restaurants: [],
        state1: '',
        state2: ''
      };
    },
    methods: {
      querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      },
      loadAll() {
        return [
          { "value": "", "address": "144" },
          { "value": "Hot honey ", "address": "661" },
          { "value": "", "address": "9886113" },
          { "value": "()", "address": "438" },
          { "value": "", "address": "96811818-101" },
          { "value": "", "address": "633" },
          { "value": "", "address": "1685" },
          { "value": "", "address": "1435" },
          { "value": "", "address": "144481B-107" },
          { "value": "", "address": "817" }
        ];
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
      
    }
  }
</script>
<style>
</style>



